.fieldset--short {
  position: relative;
}

.fieldset {
  @media (min-width: @tablet-width) {
    width: 280px;
  }
}

.fieldset:first-child {
  @media (min-width: @tablet-width) {
    padding-right: 63px;
    margin-right: 64px;
  }
}

.fieldset--wide {
  @media (min-width: @tablet-width) {
    width: 960px;
    position: relative;
    z-index: 1;
  }
}

.fieldset__title {
  margin: 0;
  padding: 0;
  padding-top: 24px;
  text-align: center;
  .text-size(18px, 26px);
  font-weight: 700;

  @media (min-width: @tablet-width) {
    .text-size(21px, 26px);
    padding-top: 0;
  }
}

.fieldset__list {
  padding: 0;
  padding-top: 14px;
  margin: 0;
  list-style: none;

  @media (min-width: @tablet-width) {
    padding-top: 28px;
  }
}

.fieldset__list--wide {
  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 30px;
    padding-left: 36px;
    padding-right: 36px;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 30px;
    padding-left: 53px;
    padding-right: 53px;
  }
}

.fieldset__list--wider {
  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding-left: 84px;
    padding-right: 84px;
    column-gap: 30px;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    padding-left: 44px;
    padding-right: 44px;
    column-gap: 30px;
  }
}

.fieldset__item--radio {
  @media (min-width: @tablet-width) {
    width: 247px;
  }
}

.fieldset__item--checkbox:not(:last-child) {
  margin-bottom: 36px;

  @media (min-width: @desktop-width) {
    margin-bottom: 20px;
  }
}

.fieldset__item--radio:not(:last-child) {
  margin-bottom: 36px;
}

.fieldset__title--radio {
  margin-bottom: 20px;
  padding-left: 65px;
  padding-right: 65px;

  @media (min-width: @tablet-width) {
    background-color: @white;
  }
}

.fieldset__title--radio::before {
  @media (min-width: @tablet-width) {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background-color: #dbdbdb;
    left: 50%;
    transform: translateX(-50%);
    top: 13px;
    z-index: -1;
  }
}

.fieldset__title--checkbox {
  margin-bottom: 20px;

  @media (min-width: @tablet-width) {
    padding-left: 60px;
    padding-right: 60px;
    background-color: @white;
  }
}

.fieldset__title--checkbox::before {
  @media (min-width: @tablet-width) {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background-color: #dbdbdb;
    left: 50%;
    transform: translateX(-50%);
    top: 13px;
    z-index: -1;
  }
}

.fieldset__label {
  .text-size(14px, 21px);
  margin-left: 16px;
}

.fieldset__input {
  width: 280px;
  padding: 16px 16px 16px 16px;
  border: none;
  background-color: @lighter-grey;
  margin-bottom: 16px;
  font-family: inherit;
  .text-size(18px, 26px);
  font-weight: 700;

  @media (min-width: @desktop-width) {
    width: 430px;
  }
}

.fieldset__input::-webkit-input-placeholder {
  color: @black;
}

.fieldset__input-short::-webkit-input-placeholder {
  color: fade(@black, 30%);
}

.fieldset__input:hover {
  background-color: #e5e5e5;
}

.fieldset__input:focus {
  outline: none;
}

.fieldset__input:active {
  background-color: @white;
  outline: 4px solid #e5e5e5;
}

.fieldset__input-short {
  padding-right: 70px;

  @media (min-width: @desktop-width) {
    width: 402px;
  }
}

.fieldset__input-phone {
  position: relative;
}

.fieldset__input-phone::after {
  content: "";
  position: absolute;
  width: 58px;
  height: 58px;
  top: 0;
  right: 0;
  padding: 20px;
  background-color: @light-grey;
  background-image: url("../img/vectors/phone.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.fieldset__input-email {
  position: relative;
}

.fieldset__input-email::after {
  content: "";
  position: absolute;
  width: 58px;
  height: 58px;
  top: 0;
  right: 0;
  padding: 20px;
  background-color: @light-grey;
  background-image: url("../img/vectors/mail.svg");
  background-repeat: no-repeat;
  background-position: center;
}
