.form-data {
  padding-left: 20px;
  padding-right: 20px;
}

@media (min-width: @tablet-width) {
  .form-data {
    display: flex;
    flex-wrap: wrap;
    padding-left: 60px;
    padding-right: 60px;
  }
}

@media (min-width: @desktop-width) {
  .form-data {
    padding-left: 120px;
    padding-right: 120px;
  }
}

.form-data__fieldset {
  position: relative;
  margin: 0;
  padding: 0;
  padding-bottom: 24px;
  border: none;
}

.form-data__fieldset:nth-child(3) {
  padding-bottom: 39px;
}

.form-data__fieldset::after {
  content: "";
  position: absolute;
  width: 320px;
  height: 1px;
  background-color: #e5e5e5;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);

  @media (min-width: @tablet-width) {
    display: none;
  }
}

.form-data__fieldset:first-child {
  @media (min-width: @tablet-width) {
    padding-right: 43px;
    margin-right: 44px;
    margin-bottom: 48px;
    // border-right: 1px solid #dbdbdb;
    padding-bottom: 0;
  }

  @media (min-width: @desktop-width) {
    padding-right: 63px;
    margin-right: 64px;
  }
}

.fieldset--short::before {
  @media (min-width: @tablet-width) {
    content: "";
    position: absolute;
    width: 1px;
    height: 338px;
    background-color: #dbdbdb;
    top: -27px;
    left: -44px;
  }

  @media (min-width: @desktop-width) {
    top: -27px;
    left: -64px;
  }
}

.form-data__feedback {
  margin: 0;
  padding: 0;
}

.form-data__footnote {
  text-align: right;
}
